import React, { createRef } from "react";
import Card from "~/components/card";
import { FormatListBlog } from "~/domain/blog";
import { Request } from "~/http/request";
import { message } from "antd";
import _uniqBy from "lodash/uniqBy";
import _debouce from "lodash/debounce";
import Style from "./style.module.scss";
import { LatestArticles } from "./components/LatestArticles";
import SiteInfo from "./components/SiteInfo";
import { Pagination } from "antd";
import qs from "qs";
import { withRouter } from "next/router";
import Script from "next/script";
import Link from "next/link";

const PAGE_SIZE = 10;

class Blogs extends React.Component {
  contentRef = createRef();
  listRef = createRef();

  constructor(props) {
    super(props);
    const {
      router,
      router: { asPath },
    } = props;
    const queryObj = qs.parse(asPath.split("?")[1]);
    this.state = {
      pageSize: this.props.pageSize || 10,
      pageNo: this.props.pageNo || 0,
      loading: false,
      current: queryObj.current ? Number(queryObj.current) : 1,
    };
  }

  componentDidMount() {
    // this.handlePageChange();
    // window.addEventListener("scroll", this.handleScroll, true);
  }

  componentWillUnmount() {
    // window.removeEventListener("scroll", this.handleScroll, true);
  }

  // 滚动加载数据
  handleScroll = _debouce((e) => {
    const { loading, count, blogs = [] } = this.state;
    let layout = document.getElementById("layout");
    if (loading || blogs.length === count) {
      return;
    }

    if (
      Math.ceil(layout.clientHeight + layout.scrollTop) >= layout.scrollHeight
    ) {
      this.handlePageChange();
    }
  }, 100);

  handlePageChange = async () => {
    this.setState({
      loading: true,
    });
    try {
      const { pageSize } = this.state;
      const current = this.state.current + 1;
      const res = await Request.getArticleList({
        pageNo: current,
        pageSize: pageSize,
      });

      const blogs = res.data["Article"].data.map((dt) => FormatListBlog(dt));
      this.setState({
        blogs: _uniqBy([...this.state.blogs, ...blogs], "id"),
        current,
        pageNo: res.data["Article"].pageNo,
        count: res.data["Article"].count,
        pageSize: res.data["Article"].pageSize,
      });
    } catch (err) {
      console.error(err);
      message.error(err.toString());
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  itemRender = (page, type, originalElemen) => {
    if (type === "page") {
      // 在翻页按钮上添加<a>便于nextjs导出静态页面
      return (
        <Link href={`/blogs/${page}`} passHref rel={`/blogs/${page}`}>
          {page}
        </Link>
      );
    }
    if (type === "prev") {
      return (
        <Link
          href={`/blogs/${page}`}
          passHref
          rel={`/blogs/${page}`}
        >
          {originalElemen}
        </Link>
      );
    }
    if (type === "next") {
      return (
        <Link
          href={`/blogs/${page}`}
          passHref
          rel={`/blogs/${page}`}
        >
          {originalElemen}
        </Link>
      );
    }
    return originalElemen;
  };

  render() {
    const { blogs = [], latestBlogs = [], total, current } = this.props;
    return (
      <div className={Style.blog}>
        <div className={Style["blog-content"]} ref={this.contentRef}>
          {blogs.map((blog) => (
            <Card key={blog.id} data={blog} />
          ))}
          <Script
            src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-6887995008287565"
            crossOrigin="anonymous"
            strategy="lazyOnload"
          />
          <ins
            className="adsbygoogle"
            style={{ display: "block", height: "100px" }}
            data-ad-format="fluid"
            data-ad-layout-key="-fb+5w+4e-db+86"
            data-ad-client="ca-pub-6887995008287565"
            data-ad-slot="7687416506"
          ></ins>
          <div className={Style["pagination"]}>
            <Pagination
              showQuickJumper={false}
              showSizeChanger={false}
              current={+current}
              total={total}
              itemRender={this.itemRender}
              // onChange={this.onChange}
            />
          </div>
        </div>
        <div className={Style["blog-sidebar"]}>
          <LatestArticles list={latestBlogs} />
          <SiteInfo />
        </div>
      </div>
    );
  }
}

export async function getStaticProps(context) {
  const { params: { page = 1 } = {} } = context;
  // 所有文章
  const pageNo = Math.max(+page, 0);
  const res = await Request.getArticleList({
    pageNo: pageNo,
    pageSize: PAGE_SIZE,
  });

  const blogs = res.data.data.map((dt) => FormatListBlog(dt));
  // 最新10篇文章
  const latestResponse = await Request.getLatestArticleList({
    pageNo: 0,
    pageSize: 10,
  });

  const latestBlogs = latestResponse.data.data.map((dt) => FormatListBlog(dt));

  return {
    props: {
      current: +page,
      blogs,
      latestBlogs: latestBlogs,
      total: res.data.count,
    },
  };
}

export async function getStaticPaths() {
  const res = await Request.getArticleList({
    pageNo: 0,
    pageSize: 9999,
  });

  const count = res.data.count;
  const pageCount = Math.ceil(count / PAGE_SIZE);
  const paths = new Array(pageCount).fill(1).map((_, index) => {
    return {
      params: { page: `${index + 1}` },
    };
  });

  return {
    paths: paths,
    fallback: "blocking", // false or 'blocking'
  };
}

export default withRouter(Blogs);
