import http from "./http";

const PRIVATE_ARTICLE = [
  "dcf7735f-1932-45d0-bce2-f28d4f4f31d1", // 个人项目
  "3f166e04-5e23-47c4-a8bb-c9e6c812ba34", // 关于  
]

const privateQueryStr = `id ValueType.nin ('${PRIVATE_ARTICLE.join(',')}')`

export const Request = {
  getArticleList: async ({ pageNo = 1, pageSize = 10 } = {}) => {
    return await http({
      method: "post",
      url: "graphql/checkless/list",
      data: {
        query: `{
                Article(pageNo: ${pageNo}, pageSize: ${pageSize}, sorts: "-updateTime,-createTime", queryStr: "${privateQueryStr}") {
                  data {
                    id
                    name
                    description
                    content
                    isDeleted
                    createTime
                    updateTime
                    likeCount
                    viewCount
                    operatorID
                    tags
                    creativeType
                    headerImg
                    operator {
                      id
                      name
                    }
                  }
                  count
                  pageSize,
                  pageNo
                }
              }
              `,
      },
    });
  },
  getLatestArticleList: async ({ pageNo = 1, pageSize = 10 } = {}) => {
    return await http({
      method: "post",
      url: "graphql/checkless/list",
      data: {
        query: `{
                Article(pageNo: ${pageNo}, pageSize: ${pageSize}, sorts: "-createTime", queryStr: "${privateQueryStr}") {
                  data {
                    id
                    name
                  }
                }
              }
              `,
      },
    });
  },

  getChecklessArticle: async (id) => {
    return await http({
      method: "post",
      url: `/graphql/checkless`,
      data: {
        query: `{
          Article(id: "${id}") {
            id
            name
            content
            category {
              id
              name
            }
            description
            isDeleted
            createTime
            updateTime
            likeCount
            viewCount
            operatorID
            tags
            headerImg
            creativeType
            operator {
              id
              name
              avatar
              description
              github
            }
          }
        }
        `,
      },
    });
  },
  getLatestArticleListByOperator: async ({ pageNo = 1, pageSize = 5, operatorID } = {}) => {
    return await http({
      method: "post",
      url: "graphql/checkless/list",
      data: {
        query: `{
                Article(pageNo: ${pageNo}, pageSize: ${pageSize}, sorts: "-createTime", queryStr: "operator.id ValueType.eq '${operatorID}' and ${privateQueryStr}") {
                  data {
                    id
                    name
                  }
                }
              }
              `,
      },
    });
  },
  getChecklessTinyCode: async (id) => {
    return await http({
      method: "post",
      url: `/graphql/checkless`,
      data: {
        query: `{
          TinyCode(id: "${id}") {
            id
            name
            javascript
            css
            html
            createTime
            updateTime
            operator {
              id
              name
              avatar
            }
          }
        }
        `,
      },
    });
  },

  getTinyCodeList: async ({ pageNo = 1, pageSize = 10 } = {}) => {
    return await http({
      method: "post",
      url: "graphql/checkless/list",
      data: {
        query: `{
                TinyCode(pageNo: ${pageNo}, pageSize: ${pageSize}, sorts: "-updateTime,-createTime", queryStr: "${privateQueryStr}") {
                  data {
                    id
                    name
                    javascript
                    css
                    html
                    createTime
                    updateTime
                    operator {
                      id
                      name
                      avatar
                    }
                  }
                  count
                  pageSize,
                  pageNo
                }
              }
              `,
      },
    });
  },
};
